<template>
  <div>
    <div class="fir" v-for="item in album_list.album" :key="item.id">
      <p>匹配专辑:</p>
      <router-link tag="div" :to="`/recommend_songs/${item.id}`"> 
      <div class="album" >
         <img :src="`${item.picUrl}`" alt=""> 
         <div>
        <p>《 {{item.name}} 》</p> 
        <p>{{item.artist.name}}</p>
        <p>{{item.company}}</p>
        </div>
      </div>
      </router-link>
    </div>
    <div class="sec">
      <p>单曲：</p>
      <song-list
        v-for="song in song_list"
        @playing="$emit('playing', $event)"
        :key="song.id"
        :song="song"
        :val="val"
        :playMusic="playMusic"
      ></song-list>
    </div>
  </div>
</template>

<script>
import SongList from "@/components/SongList";

export default {
  components: {
    SongList,
  },
  props: ["val","playMusic"],
  data() {
    return {
      song_list: [],
      album_list: "",
    };
  },
  created() {
    this.axios.get("/search?keywords=" + this.val).then((data) => {
      if (data.data.result.songs) {
        this.song_list = data.data.result.songs.map((d) => {
          d = {
            name: d.name,
            al: d.album,
            duration: d.duration,
            ar: d.artists,
            id: d.id,
          };
          return d;
        });
      }
    });

    this.$axios.get("/search/multimatch?keywords=" + this.val).then((data) => {
        this.album_list = data.data.result
      console.log(data.data.result);
    });
  },
};
</script>

<style lang="less" scoped>
div{
.fir{
    margin: 10px auto;
    .album{
        display: flex;
        align-items: center;
        // background-color: #fafafa;
        box-shadow:2px 3px 5px -4px #999,-2px 0px 5px -4px #999 ;
        padding: 5px 0;
        border-radius: 10px;
        margin: 15px 0;
      img{
          display: block;
          width: 10vh;
          height: 10vh;
          border-radius: 50%;
          margin-left: 20px;
      } 
      div{
          margin-left: 12vw;
          p{
              &:nth-child(1){
                  text-indent: -0.5em;
              }
              &:nth-child(2){
                font-size: 15px;
              }
              &:last-child{
                  font-size: 14px;
                  color: #999;
              }
          }
      } 
    }
    }
    .sec{
        // background-color: #fafafa;
        border-radius: 10px;
        box-shadow:2px 4px 5px -4px #999,-2px -2px 5px -4px #999 ;
        p{ 
            padding: 10px ;
        }
    }
}
</style>