<template>
  <div class="search">
    <form @submit.prevent.stop="search">
    <div class="nav">
      <img @click="comback" src="../assets/img/back3.png" alt="">
      <input type="search" v-model="val" placeholder="搜索" @input="inputing"/>
    </div>
    </form>

 <component @playing="$emit('playing',$event)" :is="showHot" @search="search" :playMusic="playMusic" :val="val"></component>
   
  </div>
</template>

<script>
import SearchHot from '@/components/SearchHot';
import SearchProcess from '@/components/SearchProcess';
import SearchResult from '@/components/SearchResult';

export default {
  name: "Search",
  component: {
    SearchHot,
    SearchProcess,
    SearchResult
  },
  props:['playMusic'],
  data() {
    return {
      showHot:SearchHot,
      val:""
    };
  },

  methods:{
    inputing(){
      if(this.val == "" || this.val.length <= 0){
        // console.log(1111)
      this.showHot = SearchHot;
      }else{
      this.showHot = SearchProcess;
      }
    },
    search(event){
      if(event.val){
      this.val  = event.val;

      }
      this.showHot = SearchResult;
    },
    comback(){
      this.$router.back()
    }
  }
};
</script>

<style lang="less" scoped>

.search {
  padding: 10px;

.nav{
  position: relative;
  img{
    position: absolute;
        top: 6px;
    left: 2vw;
    width: 20px;
  }
  input{
     display: block;
    width: 80%;
    border: 1px solid green;
    border-radius: 20px;
    text-align: center;
    font-size: 17px;
    line-height: 30px;
    margin: 20px auto;
    padding-right: 5px;
  }
}
 
}
</style>
