<template>
  <div>
     
    <ul>
        <p>搜索：{{val}}</p>
        <li  v-for="word in sug_list" :key="word.id" @click.stop="$emit('search',{val:word.name})" >
          {{word.name}}
           <img src="../assets/img/jian.png"> 
           </li>
        <li v-show="sug_list.length <=0">无结果</li>
    </ul>
  </div>
</template>
<script>
export default {
  props: ["val"],
  data() {
    return {
      sug_list: [],
    };
  },
  created() {
    this.suggest(this.val);
  },
  watch: {
    val(newValue) {
      this.suggest(newValue);
    },
  },
  methods: {
    suggest(val) {
      this.axios.get("/search/suggest?keywords="+ val).then((data) => {
       
        if (data.data.result && data.data.result.songs) {
          this.sug_list = data.data.result.songs;
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
ul{
  p{
    color: rgb(174, 206, 137);
    padding-bottom: 5px;

    
  }
  li{
    font-size: 14px;
    line-height: 40px;
    border-bottom:.5px solid rgb(226, 226, 226) ;
    padding-left:2.5em; 
    background: url(../assets/img/search.png)no-repeat left center;
    background-size: 30px 30px;
   position: relative;
   img{
     width: 25px;
     position: absolute;
     right: 5px;
     top: 10px;
   }

  }
}
</style>